import {NavLink} from "react-router-dom";
import React from "react";
const Menu=()=>{
return(<>
<div className="container-fluid nav-bg">
   <div  className="row">
        <div className="col-10 mx-auto">

       
          <nav className="navbar navbar-expand-lg navbar-light bg-light">
             <div className="container-fluid">
                 <NavLink className="navbar-brand" to="/">Tecnical Bilal</NavLink>
                 <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
               </button>
                   <div className="collapse navbar-collapse" id="navbarSupportedContent">
                       <ul className="navbar-nav me-auto mb-2 mb-lg-0" style={{marginLeft:"400px"}}>
                          <li className="nav-item">
                          <NavLink exact activeClassName="nav_li"  className="nav-link active" aria-current="page" to="/">Home</NavLink>
                           </li>
                           <li  className="nav-item">
                          <NavLink activeClassName="nav_li"  className="nav-link" to="/service">Service</NavLink>
                            </li>  
                            <li  className="nav-item">
                            <NavLink activeClassName="nav_li"  className="nav-link" to="/about">About</NavLink>
                        </li> 
                        <li  className="nav-item">
                            <NavLink activeClassName="nav_li"  className="nav-link" to="/contact">Contact</NavLink>
                        </li> 
                      
                        
 
                        </ul>
                            </div>
                      </div>
              </nav>
         </div>
    </div>
</div>
</>);
};
export default Menu